<template>
  <div class="time-window">
    <ul v-if="modelValue.length" class="time-window__list">
      <li v-for="(item, index) in modelValue" :key="index" class="time-window__item">
        <InputSelectButton
          class="time-window__select-btn-input"
          :options="options"
          :model-value="item.days"
          :disabled="disabled"
          @update:model-value="(val) => onUpdate(val, index, 'days')"
        />

        <span class="ty-forms-text time-window__text">С</span>
        <InputTime
          class="time-window__time-input"
          :model-value="item.from"
          :disabled="disabled"
          @update:model-value="(val) => onUpdate(val, index, 'from')"
        />

        <span class="ty-forms-text time-window__text">До</span>
        <InputTime
          class="time-window__time-input"
          :model-value="item.to"
          :disabled="disabled"
          @update:model-value="(val) => onUpdate(val, index, 'to')"
        />

        <span class="ty-forms-text time-window__text">(UTC)</span>
        <ButtonBase class="time-window__delete-btn" icon-name="deleteItem" :disabled="disabled" @click="onDeleteClick(index)" />
      </li>
    </ul>

    <ButtonBase class="time-window__add-btn" icon-name="plus" :disabled="disabled" @click="onAddClick()">
      {{ $t("uikit.time_window.add_button") }}
    </ButtonBase>
  </div>
</template>

<script setup lang="ts">
import ButtonBase from "@/shared/ui_kit/ButtonBase.vue";
import InputSelectButton from "@/shared/ui_kit/form/InputSelectButton.vue";
import InputTime from "@/shared/ui_kit/form/InputText/InputTime.vue";
import { useI18n } from "vue-i18n";
import { array, bool } from "vue-types";

const { t } = useI18n();

const props = defineProps({
  modelValue: array<{
    days: string[];
    from: string;
    to: string;
  }>().def(() => []),
  disabled: bool().def(false),
});

const emit = defineEmits<{
  (event: "update:modelValue", value: typeof props.modelValue): void;
}>();

const options = [
  { label: t("uikit.time_window.day_of_week.mon"), value: "Mon" },
  { label: t("uikit.time_window.day_of_week.tue"), value: "Tue" },
  { label: t("uikit.time_window.day_of_week.wed"), value: "Wed" },
  { label: t("uikit.time_window.day_of_week.thu"), value: "Thu" },
  { label: t("uikit.time_window.day_of_week.fri"), value: "Fri" },
  { label: t("uikit.time_window.day_of_week.sat"), value: "Sat" },
  { label: t("uikit.time_window.day_of_week.sun"), value: "Sun" },
];

const onUpdate = (val: string | string[], index: number, property: "days" | "from" | "to") => {
  const copy = JSON.parse(JSON.stringify(props.modelValue));
  copy[index][property] = val;
  emit("update:modelValue", copy);
};

const onDeleteClick = (index: number) => {
  emit(
    "update:modelValue",
    props.modelValue.filter((_, i) => i !== index)
  );
};

const onAddClick = () => {
  const emptyTimeWindow = { days: [], from: "", to: "" };
  emit("update:modelValue", [...props.modelValue, emptyTimeWindow]);
};
</script>

<style scoped lang="scss">
.time-window {
  width: 985px;
}

.time-window,
.time-window__list {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.time-window__item {
  display: flex;
  align-items: center;
  gap: 20px;
}

.time-window__select-btn-input {
  margin-right: 20px;
}

.time-window__text {
  color: var(--grey);
}

.time-window__time-input {
  width: 101px;
}

.time-window__add-btn {
  width: 100%;
}
</style>
